@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#Main{
 
  overflow: hidden;
  background-color: black; 
  
}


.App {
  text-align: center;
}

.LogoP{
  font-family:Impact;  
  font-size: 9rem;
  padding: 10px;
}

.LogoSquare{
  font-family: Pacifico;  
  padding: 10px;
}

.LogoProjects{
  font-family: Verdana;  
  padding: 10px;
}

.backgroundImage{
  background: url(/chantelletjie/p-square-projects/static/media/pieter-background.77195968.jpg);
  height: 100%;
  width: 100%;
  margin: 0;
  background-size: cover;  
  align-items: center;
  justify-content: center;

}

.parallax{
  min-height: 100%;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  
}

p{
  color:white;
  margin: 0;
}

#titleSquare.tracking-in-contract-bck-top {
	animation: tracking-in-contract-bck-top 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) 1s both;
}

#titleProject.tracking-in-contract-bck-top {
	animation: tracking-in-contract-bck-top 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) 2s both;
}

/* Title animation */
@keyframes tracking-in-contract-bck-top {
  0% {
    letter-spacing: 1em;
    transform: translateZ(400px) translateY(-300px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0) translateY(0);
    opacity: 1;
  }
}

